import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
		};
	},
	onLoad(){
		_self = this;
		//loginRes = _self.checkLogin('../cart/cart',1);  //1是跳转回tabBar页面
		//console.log(loginRes);
		//if(!loginRes) { return false; }
		this.loadData();
		//从本地数据中取数据 uni.setStorageSync('carts',code.data.carts);   var SUID = uni.getStorageSync('carts');
		
	},
	onShow() {
		_self.loadData();
		_self.cartList.forEach(item=>{
			item.loaded ='loaded';
		});
	},
	watch:{
		//显示空白页
		// cartList(e){
		// 	let empty = e.length === 0 ? true: false;
		// 	if(this.empty !== empty){
		// 		this.empty = empty;
		// 	}
		// }
	},
	computed:{
		//...mapState(['hasLogin'])
	},
	methods: {
		addPro(){
			//把商品加到购物车 _self.goodsList = _self.goodsList.concat(goodsList.data.data.data);
			let oldCart = uni.getStorageSync('carts');
			//console.log(oldCart);
			oldCart = oldCart.concat({
				id: 1,
				image: 'https://oalead.cn/touxiang.jpg',
				attr_val: '春装款 L2',
				stock: 152,
				title: 'OVBE 长袖风衣2',
				price: 2.00,
				number:2,
			});
			uni.setStorageSync('carts',oldCart);
			_self.cartList = oldCart;
			console.log(_self.cartList);
		},
		//请求数据 			async loadData(){
			//let list = await this.$api.json('cartList'); 
		loadData(){
			let list = uni.getStorageSync('carts');
			if(!list){
				console.log('没有');
				list = [];
			}
			let cartList = list.map(item=>{
				item.checked = true;
				return item;
			});
			_self.cartList = cartList;
			uni.setStorageSync('carts',cartList);
			this.calcTotal();  //计算总价
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		check(type, index){
			if(type === 'item'){
				this.cartList[index].checked = !this.cartList[index].checked;
			}else{
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item=>{
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data){
			_self.cartList[data.index].number = data.number;
			_self.calcTotal();
			uni.setStorageSync('carts',_self.cartList);
		},
		//删除
		deleteCartItem(index){
			// let list = this.cartList;
			// let row = list[index];
			// let id = row.id;
			// this.cartList.splice(index, 1);
			// this.calcTotal();
			// uni.hideLoading();
			
			_self.cartList.splice(index,1);
			_self.calcTotal();
			uni.setStorageSync('carts',_self.cartList);
			uni.hideLoading();				
		},
		//清空
		clearCart(){
			uni.showModal({
				content: '清空购物车？',
				success: (e)=>{
					if(e.confirm){
						_self.cartList = [];
						_self.total=0;
						uni.setStorageSync('carts',_self.cartList);
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			let list = this.cartList;
			if(list.length === 0){
				this.empty = true;
				_self.total=0;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item=>{
				if(item.checked === true){
					total += item.price * item.number;
				}else if(checked === true){
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder(){
			let list = this.cartList;
			let goodsData = [];
			list.forEach(item=>{
				if(item.checked){
					goodsData.push({
						attr_val: item.attr_val,
						number: item.number
					})
				}
			})

			uni.navigateTo({
				url: `/pages/order/createOrder?data=${JSON.stringify({
					goodsData: goodsData
				})}`
			})
			//this.$api.msg('跳转下一页 sendData');
		}
	}
}
